.page-header {
  position: relative;

  &--big {
    margin-bottom: 393px;

    @media (min-width: 768px) {
      margin-bottom: 534px;
    }

    @media (min-width: 1200px) {
      margin-bottom: 534px;
    }
  }

  &--small {
    margin-bottom: 164px;

    @media (min-width: 768px) {
      margin-bottom: 320px;
    }

    @media (min-width: 1200px) {
      margin-bottom: 372px;
    }
  }

  .page-header__logo {
    position: relative;
    z-index: 1000;

    img {
      width: 100%;
    }
  }
}
